import './App.css';
import { createTheme, ThemeProvider } from '@material-ui/core';
import { teal } from '@material-ui/core/colors';
import { HashRouter, Route } from 'react-router-dom';
import HomePage from 'pages/HomePage';
import BioPage from 'pages/BioPage';
import PassionPage from 'pages/PassionPage';
import ContactPage from 'pages/ContactPage';

const theme = createTheme({
  overrides: {
    MuiCard: {
      root: {
        padding: '10px',
        boxShadow: '0px 1px 1px 0px rgba(0,0,0,0.14), 0px 1px 3px 0px rgba(0,0,0,0.12)'
      }
    },
    MuiSvgIcon: {
      root: {
        paddingTop: '6px'
      }
    },
    MuiLink: {
      root: {
        color: teal[800]
      }
    },
    MuiGrid: {
      container: {
        flexGrow: 1
      }
    }
  },
  typography: {
    fontFamily: [
      '-apple-system',
      'BlinkMacSystemFont',
      '"Segoe UI"',
      'Roboto',
      '"Helvetica Neue"',
      'Arial',
      'sans-serif',
      '"Apple Color Emoji"',
      '"Segoe UI Emoji"',
      '"Segoe UI Symbol"',
    ].join(',')
  }
});

function App() {
  return (
    <ThemeProvider theme={theme}>
      <HashRouter>
          <Route exact path={["/home", "/"]}>
              <HomePage />
          </Route>
          <Route exact path="/bio">
              <BioPage />
          </Route>
          <Route exact path="/passions">
              <PassionPage />
          </Route>
          <Route exact path="/contact">
              <ContactPage />
          </Route>
      </HashRouter>
    </ThemeProvider>
  );
}

export default App;
