import { Card, Grid, createStyles, makeStyles, Typography } from '@material-ui/core';
import styled from 'styled-components';
import { map } from 'lodash';
import { LabelImportantRounded } from '@material-ui/icons';

interface ExperienceCardProps {
    positionTitle: string;
    dateFrom: string;
    dateTo?: string;
    noteItems: string[];
    lastSection?: boolean;
}

const useStyles = makeStyles(
    createStyles({
      flex: {
        display: 'flex',
        flexGrow: 1
      }
    }),
  );

const getNoteItems = (items: string[]): JSX.Element => {
    return (
        <Grid container spacing={1} direction="column">
            {
                map(items, (note) => 
                    <Grid container spacing={4} direction="row" style={{ paddingTop: '5px' }}>
                        <Grid item md={1}>
                            <LabelImportantRounded fontSize="small" style={{ color:'teal', padding: 0 }} />
                        </Grid>
                        <Grid item md={11}>
                            {note}
                        </Grid>
                    </Grid>
                )
            }
        </Grid>
    );
}

function ExperienceCard(props: ExperienceCardProps){
    const classes = useStyles();
    const { positionTitle, dateFrom, dateTo, noteItems, lastSection } = props;

    return (
        <Grid container className={classes.flex} spacing={1} direction="column">
            <Grid item>
                <Grid container className={classes.flex} spacing={5} direction="row">
                    <Grid item>
                        <Typography>{`${dateFrom} - ${dateTo}`}</Typography>
                    </Grid>
                    <Grid item>
                        <Typography>{positionTitle}</Typography>
                    </Grid>
                </Grid>
            </Grid>
            <Grid item>
                {getNoteItems(noteItems)}
            </Grid>
        </Grid>
    );
}

export default ExperienceCard;