import React from 'react';
import * as am4core from "@amcharts/amcharts4/core";
import * as am4maps from "@amcharts/amcharts4/maps";
import am4geodata_worldLow from "@amcharts/amcharts4-geodata/worldLow";
import { useDispatch, useStore } from 'react-redux';
import VISITED_PLACES from 'constants/VisitedPlaces';
import { DEFAULT_LOCATION_MARKER_COLOR,
         SELECTED_LOCATION_MARKER_COLOR,
         LOCATION_MARKER_STROKE_COLOR,
         MAP_REGION_DEFAULT_COLOR,
         MAP_REGION_HOVER_COLOR } from 'constants/Constants';

function Map() {
    const dispatch = useDispatch();
    const store = useStore();

    React.useEffect(() => {
        let map = am4core.create("chartdiv", am4maps.MapChart);
        map.geodata = am4geodata_worldLow;
        map.projection = new am4maps.projections.NaturalEarth1();

        let polygonSeries = new am4maps.MapPolygonSeries();
        map.series.push(polygonSeries);
        polygonSeries.useGeodata = true;
        polygonSeries.exclude = ["AQ"];

        let polygonTemplate = polygonSeries.mapPolygons.template;
        polygonTemplate.fill = am4core.color(MAP_REGION_DEFAULT_COLOR);
        let hs = polygonTemplate.states.create("hover");
        hs.properties.fill = am4core.color(MAP_REGION_HOVER_COLOR);

        let imageSeries = map.series.push(new am4maps.MapImageSeries());
        let imageSeriesTemplate = imageSeries.mapImages.template;
        let circle = imageSeriesTemplate.createChild(am4core.Circle);
        circle.radius = 5;
        circle.fill = am4core.color(DEFAULT_LOCATION_MARKER_COLOR);
        circle.stroke = am4core.color(LOCATION_MARKER_STROKE_COLOR);
        circle.strokeWidth = 2;
        circle.nonScaling = true;
        circle.tooltipText = "{name}";
        circle.clickable = true;

        circle.events.on("inited", function(ev) {
          let dataContext: any = ev.target.dataItem?.dataContext;
          if (dataContext.name !== store.getState().selectedLocation) {
            ev.target.fill = am4core.color(DEFAULT_LOCATION_MARKER_COLOR);
          } else {
            ev.target.fill = am4core.color(SELECTED_LOCATION_MARKER_COLOR);
          }
        });

        circle.events.on("hit", function(ev) {
            let dataContext: any = ev.target.dataItem?.dataContext;
            dispatch({type: 'CHANGE_LOCATION', payload: dataContext.name});
            imageSeries.mapImages.values.forEach(
              mapImage => {
                if (ev.target !== mapImage.children.values[0]) {
                  mapImage.children.values[0].fill = am4core.color(DEFAULT_LOCATION_MARKER_COLOR);
                }
            });
        });

        circle.events.on("over", function(ev) {
            ev.target.fill = am4core.color(SELECTED_LOCATION_MARKER_COLOR);
        });

        circle.events.on("out", function(ev) {
            let dataContext: any = ev.target.dataItem?.dataContext;
            if (dataContext.name !== store.getState().selectedLocation) {
                ev.target.fill = am4core.color(DEFAULT_LOCATION_MARKER_COLOR);
            } else {
                ev.target.fill = am4core.color(SELECTED_LOCATION_MARKER_COLOR);
            }
        });

        imageSeriesTemplate.propertyFields.latitude = "latitude";
        imageSeriesTemplate.propertyFields.longitude = "longitude";
        imageSeries.data = VISITED_PLACES;

        return () => {
            map.dispose();
        }
    }, []);
    
    return (<div style={{height: '100%', width: '100%'}} id="chartdiv"></div>);
}

export default Map;